[data-scroll="true"] {
  overflow-y: auto;

  @include respond-to("1024->") {
    overflow: hidden;
  }
}

.about-page {
  position: relative;

  height: 100vh;
  // min-height: 100vh;

  padding: var(--padding-container);
  background-color: HSL(var(--primary-hs) 100%);
}

.about {
  position: relative;

  // width: auto;
  // min-height: 0;
  height: 100%;

  border: 2px solid HSL(var(--primary-hs) 0%);

  overflow: hidden;

  background: var(--clr-bg);
  background-image: url("../img/about-bg.png"), url("../img/svg/about-rays.svg");
  background-repeat: no-repeat;
  background-size: 100%, cover;
  background-position: bottom 0px left 65%;

  @include respond-to("568->") {
    background-size: 112%, cover;
    background-position: bottom, center;
  }

  @include respond-to("736->") {
    background-size: 100%, cover;
  }

  @include respond-to("1024->") {
    background-size: 100%, 150%;
    background-position: bottom -6.9vw right, top -1vw center;
  }

  @include respond-to("1366->") {
    background-position: bottom -9vw right, top -1vw center;
  }

  &__content {
    position: relative;
    // display: -webkit-box;

    height: 100%;
    z-index: 1;

    overflow: hidden;
  }
}

.story {
  --gap: 0;
  grid-template-columns: minmax(2rem, 1fr) repeat(10, minmax(0, 10rem)) minmax(
      2rem,
      1fr
    );
  grid-template-rows: minmax(0, 1rem) repeat(4, 1fr);
  height: 100%;
  // place-self: end;
  transition: transform 1s cubic-bezier(0.93, 0.02, 0.56, 0.95);

  @include respond-to("568->") {
    // grid-template-areas:
    //   ". . . . . ."
    //   ". about-doodle-1 about-photo about-photo about-photo about-photo"
    //   ". about-doodle-2 about-photo about-photo about-photo about-photo"
    //   ". about-doodle-3 about-photo about-photo about-photo about-photo";
    grid-template-rows: minmax(2rem, 1fr) repeat(17, 1fr);
  }

  // @include respond-to("736->") {
  //   // grid-template-rows: minmax(0, 4rem) repeat(3, min-content) 1fr 7vh;
  // }

  @include respond-to("1024->") {
    // grid-template-rows: minmax(0, 4rem) repeat(3, min-content) 1fr 2vh;
    grid-template-columns: minmax(2rem, 1fr) repeat(9, minmax(0, 10rem)) minmax(
        2rem,
        1fr
      );
    grid-template-rows: minmax(2rem, 1fr) repeat(8, 1fr) minmax(2rem, 1fr);
  }

  &__box {
    position: relative;

    grid-column: 2 / -2;
    grid-row: 2 / span 2;

    display: flex;
    gap: 20rem;

    // overflow: hidden;

    @include respond-to("568->") {
      position: relative;
      grid-column: 2 / 9;
      grid-row: 11 / span 3;
      align-self: end;
      // margin-bottom: 0.5rem;
      flex-direction: column;
      justify-content: center;
      gap: 0;
      // display: block;
      // display: grid;
      // grid-template-columns: minmax(2rem, 1fr) repeat(10, minmax(0, 10rem)) minmax(
      //     2rem,
      //     1fr
      //   );
      // grid-template-rows: minmax(0, 2rem) repeat(4, 1fr);
      overflow: visible;
      // background: #2e3cf56e;

      @include respond-to("1024->") {
        // display: grid;
        grid-row: 6 / span 3;
        grid-column: 1 / 8;

        // grid-template-columns: minmax(2rem, 1fr) repeat(9, minmax(0, 10rem)) minmax(
        //     2rem,
        //     1fr
        //   );
        // grid-template-columns: repeat(auto-fit, minmax(5rem, 1fr));
        // grid-template-rows: minmax(2rem, 1fr) repeat(8, 1fr) minmax(2rem, 1fr);
        // justify-content: normal;
        // align-self: auto;
        // margin-bottom: 0;
      }

      @include respond-to("1366->") {
        grid-row: 5 / span 3;
      }
    }

    & > svg {
      width: 100%;
      // max-width: 65rem;
      height: max-content;

      flex: 1 0 100%;
      align-self: center;
      margin-inline: auto;
      // outline: 2px solid blue;
      // min-width: 50rem;
      // grid-area: about-doodle-1;
      text {
        // text-align: center;
        text-anchor: start;
        font-size: 2.2rem;
        font-family: var(--ff);
      }

      @include respond-to("568->") {
        // position: absolute;
        height: auto;
        width: Min(112%, 55rem);

        // flex: 1 0 30%;
      }

      @include respond-to("1024->") {
        align-self: auto;
        // width: 100%;
        height: auto;
        // width: 80%;
        // min-width: 53.3rem;
        // width: 65%;
        // min-width: 53rem;
        width: Min(100%, 64rem);
      }

      @include respond-to("1366->") {
        // width: 57%;
        // min-width: 62rem;
        width: Min(100%, 80rem);
      }

      @include respond-to("1920->") {
        // width: 80%;
        // min-width: 50rem;
      }
    }
  }

  &__photo {
    & img {
      width: 100%;
      height: 100%;
      // outline: 2px solid green;
      // object-fit: cover;
    }

    &--person {
      grid-column: 2 / -2;
      grid-row: 4 / span 2;
      // grid-area: about-photo;

      //   width: Min(30rem, 75vw);

      height: fit-content;
      // margin-bottom: 0.5rem;
      align-self: flex-end;
      z-index: 4;
      // outline: 2px solid green;
      // transform: translate(0, -3%);

      // & img {
      //   // transform: translateY(-3rem);
      // }

      @include respond-to("320->") {
        // grid-row: 3 / span 5;
      }

      @include respond-to("568->") {
        grid-row: 15 / span 2;
        // grid-row: 4;
        // grid-column: 4 / span 8;
        grid-column: 6 / span 6;
        z-index: 1;
        transform: translate(11rem, 0.9rem);
      }

      @include respond-to("1024->") {
        grid-column: 7 / span 6;
        grid-row: 7 / span 3;
        margin-bottom: 0;

        // align-self: flex-end;
        transform: translate(0, 0);
      }
    }

    &--doodle1 {
      grid-row: 2;
      grid-column: 2 / -2;
      // height: 15rem;

      z-index: 4;

      @include respond-to("568->") {
        grid-column: 3 / span 6;
        align-self: end;
        transform: translate(8rem, 7rem);
      }

      @include respond-to("1024->") {
        grid-column: 1 / span 8;
        grid-row: 1;
        // transform: translate(0, 1vw);
        transform: translate(13rem, 8rem);
      }

      @include respond-to("1366->") {
        transform: translate(13rem, 18rem);
      }
    }

    &--doodle2 {
      grid-column: 2 / -2;
      grid-row: 3;
      z-index: 3;
      // transform: translate(0, -1.5rem);

      // height: 15rem;
      @include respond-to("568->") {
        grid-column: 2 / span 6;
        align-self: end;
        transform: translate(-2rem, 5rem);
      }

      @include respond-to("1024->") {
        grid-column: 1 / span 8;
        grid-row: 4;
        transform: translate(0rem, 6rem);
      }

      @include respond-to("1366->") {
        transform: translate(0rem, 16rem);
      }

      // @include respond-to("1920->") {
      //   transform: translate(0rem, 13rem);
      // }
    }

    // &--doodle3 {
    //   grid-column: 2 / -2;
    //   grid-row: 4;
    //   z-index: 2;

    //   @include respond-to("568->") {
    //     grid-column: 1 / span 7;
    //     // transform: translate(-5rem, -8.5rem);
    //   }

    //   @include respond-to("1024->") {
    //     grid-column: 1 / span 8;
    //     grid-row: 6;
    //     transform: translate(9rem, 0rem);
    //   }

    //   @include respond-to("1366->") {
    //     transform: translate(10rem, 4rem);
    //   }

    //   @include respond-to("1920->") {
    //     transform: translate(8rem, 3rem);
    //   }
    // }
  }

  &__content {
    grid-column: 2 / 8;
    grid-row: 1 / 4;

    // min-width: 45rem;

    font-family: var(--ff);
    // outline: 4px solid red;

    &-box {
      position: relative;
      width: 100%;
      height: 100%;

      & div > p {
        text-indent: 2rem;
        width: 85%;
        margin: 0 auto;
        line-height: 1.2;
      }

      & div {
        width: 100%;
        height: 100%;
        background: radial-gradient(
          ellipse at center,
          rgb(255, 255, 255) 0%,
          rgb(253, 253, 253) 70%,
          rgba(255, 255, 255, 0) 70.3%
        );
      }
    }
  }

  &__dots {
    position: absolute;
    bottom: 10%;
    left: 50%;
    transform: translate(-50%, -10%);

    width: 100%;
    justify-content: space-between;

    z-index: 5;

    @include respond-to("568->") {
      display: none;
    }

    // button {
    //   @include flex(null, center, center);

    //   width: 4rem;
    //   aspect-ratio: 1;

    //   background-color: transparent;
    //   background-image: url("../img/svg/circle4.svg");
    //   background-repeat: no-repeat;

    //   border: none;
    // }
  }

  // &--1,
  // &--2 {
  //   // & button {
  //   //   opacity: 0;
  //   //   visibility: hidden;
  //   //   // transition: all 0.3s ease;
  //   // }
  //   // transition: opacity 4s linear, transform 0.5s ease;
  //   // transform: translate(0, 0);
  // }

  &--2 {
    position: absolute;
    inset: 0;
    opacity: 0;
    // transition: opacity 0.6s linear;
    & button {
      opacity: 0;
      visibility: hidden;
      // transition: all 0.3s ease;
    }

    @include flex(column, null, null);

    width: 100%;
    height: 100%;
    background-color: var(--clr-footer);
    // transform: translate(100%, 0);
    z-index: 5;

    overflow: hidden;

    &__bg1 {
      height: 50%;
      background-image: url("../img/svg/yellow_rays.svg");
      background-repeat: no-repeat;
      background-size: 400%;
      background-position: center;
      background-color: var(--clr-primary);

      @include respond-to("568->") {
        background-size: 130%;
      }
    }

    &__bg2 {
      height: 50%;
    }

    &__door {
      display: flex;
      width: 100%;
      height: 100%;

      overflow: hidden;

      @include respond-to("568->") {
        display: block;
      }

      & svg {
        display: block;
        width: 100%;
        max-width: 100%;
        height: auto;
        margin: auto 0 0 auto;
        transform: translateY(3.4vh);
        overflow: visible;

        & .cls-1 {
          fill: #88b5c0;
        }

        & .cls-2 {
          fill: #4e5778;
        }

        & .cls-3 {
          fill: #1d1d1b;
        }

        & .cls-4 {
          fill: #f9e69a;
        }

        & .cls-5 {
          fill: #ee7950;
        }

        & .cls-6 {
          fill: HSL(var(--primary-hs) 100%);
        }

        // & .it {
        //   fill: purple;
        // }

        @include respond-to("568->") {
          width: auto;
          height: 100%;
          margin-left: auto;
          // transform: translateY(3.4vh);
        }
      }
    }

    &__face {
      // @include flex(null, null, null);
      position: fixed;
      width: 100%;
      height: 100%;

      &-img {
        @include flex(column, flex-end, flex-end);
        width: 100%;
        height: 100%;

        background-image: url("../img/svg/dots_orange.svg");
        background-repeat: no-repeat;
        // background-size: cover;
        background-position: bottom -6rem left -4rem;

        @include respond-to("568->") {
          @include flex(row, space-between, flex-end);
          background-position: bottom -20rem right 19rem;
        }

        @include respond-to("1024->") {
          @include flex(null, null, center);
          background-position: bottom -17rem left -16rem;
        }

        & img {
          // width: Min(50vw, 100vw);
          // height: 170%;

          width: 80vw;
          order: 1;

          transform: translate(-34vw, Max(20%, 11vh));
          // transform: translate(-17vw, clamp(1rem, 28vw, 12rem));

          @media (max-width: 20em) {
            transform: translate(-34vw, clamp(1rem, 28vw, 12rem));
          }

          @include respond-to("568->") {
            order: 0;
            transform: translate(-17vw, Max(20%, 11vh));
          }

          @include respond-to("1024->") {
            width: Min(56vw, 100vw);

            transform: translate(0, 0);
          }
        }

        & svg {
          height: 100%;
          transform: translate(-19vw, 9vh);
          overflow: visible;

          & .cls-1 {
            fill: HSL(var(--primary-hs) 100%);
          }

          & .cls-2 {
            fill: #1d1d1b;
          }
        }

        .face-doodle-desktop {
          display: none;
          height: auto;

          & text {
            text-anchor: start;
            font-size: 1rem;
            font-family: var(--ff);
          }

          @include respond-to("568->") {
            display: block;
          }

          @media (min-width: 35.5em) and (max-width: 64em) {
            min-width: clamp(34rem, 60vw, 60rem);
            // height: auto;
            transform: translate(-45vw, -5vh);
          }

          @include respond-to("1024->") {
            min-width: 50vw;
          }
        }

        .face-doodle-mobile {
          display: block;
          width: 95%;
          height: auto;
          transform: translate(0, Max(120%, 30vh)) scale(-1, 1); //48
          min-height: 30rem;

          z-index: 2;

          & text {
            text-anchor: start;
            font-size: 0.9rem;
            font-family: var(--ff);
            transform: scale(-1, 1);
          }

          @media (max-width: 20em) {
            transform: translate(0, Max(20%, 20vh)) scale(-1, 1); //24
          }

          @include respond-to("568->") {
            display: none;
          }
        }
      }
    }

    &__doodle {
      @include flex(null, null, center);
      width: 100%;
      height: 100%;
    }
  }
}

